<template>
  <div class="expand_row">
    <Table ref="selectionTable" 
      :columns="columns"
      :data="bodyList">
      <template slot-scope="{ row }" slot="name">
        <span>{{ row.name }}</span>
      </template>
      <template slot-scope="{ row }" slot="age">
        <span>{{ row.name }}</span>
      </template>
      <template slot-scope="{ row }" slot="address">
        <span>{{ row.name }}</span>
      </template>
      <template slot="action" v-if="btn">
        <div>
          <Button type="primary"
           v-for="item in btn"
           :key="item.type"
           @click="btnOperate(item.type)">{{item.text}}</Button>
        </div>
      </template>
    </Table>
  </div>
</template>

<script>
export default {
  name: 'expand_row',
  props: {
    btn: {
      type: Array
    }
  },
  data () {
    return {
      columns: [
        {
          title: '编号',
          slot: 'name'
        },
        {
          title: '案号',
          slot: 'name'
        },
        {
          title: '申请人',
          slot: 'age'
        },
        {
          title: '被申请人',
          slot: 'age'
        },
        {
          title: '纠纷类型',
          slot: 'age'
        },
        {
          title: '案件状态',
          slot: 'age'
        },
        {
          title: '申请时间',
          slot: 'age'
        },
        {
          title: '开庭时间',
          slot: 'age'
        }
      ],
      bodyList: [
        {
          name: '王小明',
          age: 18,
          birthday: '919526400000',
          address: '北京市朝阳区芍药居'
        },
        {
          name: '张小刚',
          age: 25,
          birthday: '696096000000',
          address: '北京市海淀区西二旗'
        }
      ]
    }
  },
  mounted () {
    if (this.btn) {
      let tmp = { title: '操作', slot: 'action' }
      this.columns.push(tmp)
    }
  },
  methods: {
    btnOperate (type) {
      this.$emit('operate', type)
    }
  }
}
</script>