<template>
  <div class="merge_box">
    <div class="case_type">
      <Button type="primary"
        v-for="item in caseTypeList"
        @click="changeCaseType(item.value)"
        :key="item.value">
        {{item.label}}
      </Button>
    </div>
    <div class="content">
      <div class="search_box">
        <Input search enter-button
          v-model="searchValue"
          @on-search="goSearch" 
          style="width: 200px;"
          placeholder="案件/案件编号/申请人" />
          <Button type="primary" @click="btnOperate(1)">合并审理</Button>
      </div>
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          :columns="columns"
          :data="bodyList">
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="age">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="address">
            <span>{{ row.name }}</span>
          </template>
        </Table>
        <!-- 分页 -->
        <Page class="mgt20" :total="pageObj.total"
          :page-size="pageObj.pageSize"
          :page-size-opts="pageObj.pageSizeOptions" 
          show-elevator
          show-sizer 
          @on-page-size-change="pageSizeChange"
          @on-change="pageNumChange"/>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from '../mixin'
export default {
  name: 'merge_box',
  mixins: [mixin],
  data () {
    return {
      caseTypeList: [
        {value: 1, label: '已立案案件'},
        {value: 2, label: '组庭中'},
        {value: 3, label: '已组庭案件'},
      ],
      searchValue: null,
      columns: [
        {
          type: 'selection',
          width: '50'
        },
        {
          title: '标题',
          slot: 'name'
        },
        {
          title: '申请时间',
          slot: 'age'
        },
        {
          title: '开庭时间',
          slot: 'age'
        },
        {
          title: '案件状态',
          slot: 'age'
        }
      ],
      bodyList: [
        {
          name: '王小明',
          age: 18,
          birthday: '919526400000',
          address: '北京市朝阳区芍药居'
        },
        {
          name: '张小刚',
          age: 25,
          birthday: '696096000000',
          address: '北京市海淀区西二旗'
        }
      ],
    }
  },
  mounted () {
    this.deletSelectAllHtml()
  },
  methods: {
    // 删除header的全选
    deletSelectAllHtml () {
      document.querySelector('.merge_box .ivu-table-header thead').children[0].cells[0].innerHTML = ''
    },
    // 切换案件类型
    changeCaseType (type) {
      console.log(type)
    },
    // 搜索
    goSearch () {
      console.log()
    },
    // 合并审理
    btnOperate (type) {
      console.log(type)
    },
  }
}
</script>
<style lang="less" scoped>
.merge_box {
  .case_type {
    margin-bottom: 20px;
    button {
      margin-left: 10px;
      &:first-child {
        margin-left: 0;
      }
    }

  }
  .content {
    background: #fff;
    border-radius:4px;
    .search_box {
      display: flex;
      justify-content: space-between;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
</style>