export default {
  data () {
    return {
      pageObj: {
        pageIndex: 0,
        pageSize: 50,
        pageSizeOptions: [50,100,150,200],
        total: 0,
      }
    }
  },
  methods: {
    // 修改每页数量
    pageSizeChange (num) {
      this.pageObj.pageSize = num
      this.getList()
    },
    // 翻页
    pageNumChange (num) {
      this.pageObj.pageSize = num
      this.getList()
    }
  }
}